:root,
html[data-theme=light] {
  box-sizing: border-box;
  --design-animation-default: all 0.2s ease-in-out;
  --border-radius: .3rem;
  --base-unit: 1rem;
  --font-text: "Ibm Plex Sans",
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Helvetica,
    Arial,
    sans-serif,
    Apple Color Emoji,
    Segoe UI Emoji,
    Segoe UI Symbol;
  --font-headings: "IBM Plex Sans",
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Helvetica,
    Arial,
    sans-serif,
    Apple Color Emoji,
    Segoe UI Emoji,
    Segoe UI Symbol;
  --font-mono: "SFMono-Regular",
    Consolas,
    "Liberation Mono",
    Menlo,
    Courier,
    monospace;
}

:root {
  --color-background-default: #e5e5e5;
  --color-background-default-contrast: #161718;
  --color-text-primary: #161718;
  --color-text-primary-contrast: #e5e5e5;
  --color-headings-primary: #161718;
  --color-link-primary: #3300f7;
  --color-link-hover: crimson;
  --color-link-current: black;
  --color-highlight-default: aliceblue;
  --color-highlight-extra: rgb(249 249 249);
  --color-highlight-warn: #f3e4e4;
  --color-highlight-friendly: linear-gradient(45deg, rgb(225 255 229), rgb(241 238 206));
  --color-highlight-friendly-text: #285428;
  --color-highlight-gray: #ebecec;
  --color-highlight-special: rgb(17 220 175 / 14%);
  --design-shadow-primary: rgba(84, 102, 120, 0.396) 0px 20px 30px -10px;
  --show-modifier-light: initial;
  --show-modifier-dark: none;
  transition: var(--design-animation-default);
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  color: var(--color-text-primary);
  font-family: var(--font-text);
  font-weight: 400;
  font-style: normal;
  font-size: var(--base-unit);
  background: var(--color-background-default);
  hyphens: auto;
}

// Typography

a {
  text-decoration: none;
  color: var(--color-link-primary);
  transition: var(--design-animation-default);
}

a:hover {
  color: var(--color-link-hover);
}

a.current {
  color: var(--color-link-current);
}

p {
  font-size: calc(var(--base-unit)*1);
  line-height: calc(var(--base-unit)*1.4);
}

h1,
h2,
h3 {
  color: var(--color-headings-primary);
  font-family: var(--font-headings);
}

h1 {
  font-size: calc(var(--base-unit)*1.4);
}

h2 {
  font-size: calc(var(--base-unit)*1.3);
}

h3 {
  font-size: calc(var(--base-unit)*1.2);
}

// Sections

.section-nav {
  display: grid;
  align-items: center;
  grid-template-columns: auto 1fr;
  gap: calc(var(--base-unit)*1);
  padding: calc(var(--base-unit)*2);

  &__logotype {
    img {
      height: calc(var(--base-unit)*4);
    }
  }

  &__text {
    font-family: var(--font-headings);
  }
}

// Layout

.section-intro {
  background: var(--color-background-default-contrast);
  color: var(--color-text-primary-contrast);

  h1,
  h2,
  h3 {
    color: var(--color-text-primary-contrast);
  }
}

.section-spectrum {
  padding: calc(var(--base-unit)*2);
}

.container-top {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  padding: calc(var(--base-unit)*2);

  .focus {
    grid-area: 1 / 1 / 2 / 2;

    &__main {
      margin: 0 0 calc(var(--base-unit)*2) 0;
      font-size: calc(var(--base-unit)*2);
      hyphens: none;
      &::after {
        display: block;
        width: 5rem;
        height: .3rem;
        background-color: red;
        content: "";
        margin-top: calc(var(--base-unit)*2);
      }
    }
  }

  .image {
    grid-area: 1 / 1 / 2 / 2;
  }
}